// ** React Imports
import { Link } from 'react-router-dom'

// ** Store & Actions
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import {MoreVertical, FileText, Trash2, Archive} from 'react-feather'
import {deleteGallery, getGallery} from "../store/action"

// ** Renders Client Columns
const renderImage = row => {
    const stateNum = Math.floor(Math.random() * 6),
        states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
        color = states[stateNum]

    if (row?.image_url) {
        return <img className='mr-1' src={row?.image_url} width='50' height='50' />
    } else {
        return <img color={color || 'primary'} className='mr-1' content={row.name || 'John Doe'} initials />
    }
}

const statusObj = {
    pending: 'light-warning',
    null: 'light-success',
    inactive: 'light-secondary'
}

export const columns = [
    {
        name: 'Hình ảnh',
        minWidth: '40px',
        selector: 'fullName',
        sortable: true,
        cell: row => renderImage(row)
    },
    {
        name: 'Bộ sưu tập',
        minWidth: '300px',
        selector: 'name',
        sortable: true,
        cell: row => (
            <div className='d-flex justify-content-left align-items-center'>
                <div className='d-flex flex-column'>
                    <Link
                        to={`/gallery/detail/${row.id}`}
                        className='user-name text-truncate mb-0'>
                        <span className='font-weight-bold'>{row.name}</span>
                    </Link>
                </div>
            </div>
        )
    },
    {
        name: 'Người tạo',
        minWidth: '150px',
        selector: 'category',
        sortable: true,
        cell: row => row?.name
    },
    {
        name: 'Email',
        minWidth: '320px',
        selector: 'email',
        sortable: true,
        cell: row => row?.email
    },
    {
        name: 'Ngày tạo',
        minWidth: '320px',
        selector: 'email',
        sortable: true,
        cell: row => row?.createdAt
    },
    {
        name: 'Trạng thái',
        minWidth: '138px',
        selector: 'status',
        sortable: true,
        cell: row => (
            <Badge className='text-capitalize' color={statusObj[row.deletedAt]} pill>
                {row?.is_active ? 'Hiển thị' : 'Ẩn đi'}
            </Badge>
        )
    },
    {
        name: 'Hành động',
        minWidth: '100px',
        cell: row => (
            <UncontrolledDropdown>
                <DropdownToggle tag='div' className='btn btn-sm'>
                    <MoreVertical size={14} className='cursor-pointer' />
                </DropdownToggle>
                <DropdownMenu right>
                    <DropdownItem
                        tag={Link}
                        to={`/gallery/detail/${row.id}`}
                        className='w-100'
                        // onClick={() => store.dispatch(getSocialStory(row.id))}
                    >
                        <FileText size={14} className='mr-50' />
                        <span className='align-middle'>Details</span>
                    </DropdownItem>
                    <DropdownItem
                        tag={Link}
                        to={`/gallery/edit/${row.id}`}
                        className='w-100'
                        // onClick={() => store.dispatch(getSocialStory(row.id))}
                    >
                        <Archive size={14} className='mr-50' />
                        <span className='align-middle'>Edit</span>
                    </DropdownItem>
                    <DropdownItem className='w-100' onClick={() => store.dispatch(deleteGallery(row.id))}>
                        <Trash2 size={14} className='mr-50' />
                        <span className='align-middle'>Delete</span>
                    </DropdownItem>
                </DropdownMenu>
            </UncontrolledDropdown>
        )
    }
]
